.squealer {
    font-family: 'Squealer', sans-serif;
}

.anton {
    font-family: "Anton", sans-serif;
    
}

* {
    font-family: "Lato", sans-serif;
    font-weight: 300;
    font-style: normal;
    font-size: 20px;
}

a {
    color: #ff3333;
    text-decoration: none;
    transition: all ease-in-out 0.3s;
}

a:hover {
    color: white;
    text-shadow: 0 0 10px #ff3333, 0 0 20px #ff3333, 0 0 30px #ff0000;
}

h1.huge {
    font-size: 80px;
    color: white;
    -webkit-text-stroke: 2px black;
}

/* Navbar sticky */
.navbar.sticky-top {
    position: sticky !important;
    top: 0;
    z-index: 1050;
    background-color: #0d0f11;
}

.nav-item {
    transition: all 0.3 ease-in-out;
}

.nav-link.active {
    color: #fff;
    /* colore base del testo */
    text-shadow: 0 0 5px #e60000 !important;
}

.nav-link:hover {
    text-shadow: 0 0 5px #e60000 !important;
}

.glow-pulse {
    color: #e60000;
    /* colore base del testo */
    text-shadow: 0 0 5px #e60000;
    animation: glowPulse 2s infinite;
}

@keyframes glowPulse {
    0% {
        text-shadow: 0 0 5px #e60000, 0 0 10px #e60000, 0 0 20px #e60000;
    }

    50% {
        text-shadow: 0 0 20px #ff1a1a, 0 0 40px #ff1a1a, 0 0 60px #ff1a1a;
    }

    100% {
        text-shadow: 0 0 5px #e60000, 0 0 10px #e60000, 0 0 20px #e60000;
    }
}

.clickable {
    cursor: pointer;
}

.card {
    background-color: #131518;
}

.card-date {
    background-color: #0d0f11;
}

.btn-primary {
    background-color: #ff1a1a;
    transition: all ease-in-out 0.3s;
}

.shadow-hover {
    transition: box-shadow 0.3s ease-in-out;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
}

.shadow-hover:hover {
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.5);
    cursor: pointer;
}

.image-text {
  font-size: 20vw;
  font-weight: bold;
  text-transform: uppercase;
  background-size: cover;
  background-position: center;
  color: transparent;
  -webkit-background-clip: text; /* necessario per Chrome, Safari, Edge */
  background-clip: text;
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
}

.bg-title {
  background-color: #0d0f11;
}

.bg-grad {
  background-color: #000000;
  background-image: linear-gradient(to bottom, #0d0f11, #212529);
}